@media only screen and (min-width: 1051px) {

  .contenedor1_footer {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
  }

  .contenedor1_footer div {
    width: 25%;
  }

  .contenedor1_footer > div > h5 {
    font-weight: bolder;
    color: #2B2B2B;
    text-align: left;
    margin-left: 25%;
  }

  .contenedor1_footer > div > p {
    font-size: 10pt;
    font-weight: lighter;
    color: #7D7D7D;
    text-align: left;
    margin-left: 25%;
  }

  .contenedor1_footer > div > p:hover {
    color: #333333;
  }

  .samp_PBX_footer {
    font-weight: bold;
    color: #4F4F4F;
  }

  .espacio_blanco_footer {
    width: 100%;
    height: 15%;
  }

  .siguenos_redes_footer div{
    display: flex;
    margin-left: 25%;
    gap: 20px;
  }

  .siguenos_redes_footer div a img {
    width: 22px;
    height: 22px;
  }

  .terminos_y_condiciones_footer {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    text-align: center;
    gap: 20px; /* Ajusta este valor para cambiar la separación */
  }

  .agrupando_todo_el_footer{
    background-color: #bdc3c7;
    box-sizing: border-box;



  }
}
