@media only screen and (min-width: 1051px) {

  .contenedor_sesion_productos {
    width: 80% !important;
    margin: 0 auto !important;
    display: flex !important;
    justify-content: space-between !important;
    overflow: hidden !important;
    height: 25% !important;
  }
  .c_s_p_divs{
    width: 100% !important;
    height: 25% !important;
    margin: 5px !important;
    transition: all 500ms ease!important;
  }
  .c_s_p_divs_img {
    width: 100% !important;
    height: 100% !important;
  }
  .c_s_p_divs_img:hover {
    transform: scale(1.1);
  }

  .contenedor2_sesion_productos{
    text-align: center;
  }
  
  
  
  /* Estilos para el div de filtrado */
  .filtrado_para_los_productos {
    float: left;
    position: initial;
    width: 100%;
  
  }
  
  .filtrado_para_los_productos div div{
    text-align: left;
    font-size: 17px;
    margin-left: 2px;
    padding: 2px;
  }
  .filtrado_para_los_productos div h4{
    text-align: left;
    margin-left: 10px;
  }
  
  .filtrado_para_los_productos div input[type='checkbox']{
    font-size: 10px;
  }

  .filtrado_para_los_productos div label{
    font-size: 15px;
  }

  .color_f_p_l_p div{
    width: 45%;
    height: 35px;
    display: flex;
    justify-content: space-between;
    gap: 10px;
    margin: 10px;
  }
  .c1_f_p_l_p{
    background-color: red;
    height: 100%;
    content: "Rojo";
  }
  .c2_f_p_l_p{
    background-color: blue;
    height: 100%;
  }
  .c3_f_p_l_p{
    background-color: burlywood;
    height: 100%;
  }
  .c4_f_p_l_p{
    background-color: green;
    height: 100%;
  }
  .c5_f_p_l_p{
    background-color: purple;
    height: 100%;
  }
  .c6_f_p_l_p{
    background-color: chocolate;
    height: 100%;
  }
  .c7_f_p_l_p{
    background-color: darksalmon;
    height: 100%;
  }
  .c8_f_p_l_p{
    background-color: darkgrey;
    height: 100%;
  }
  .c9_f_p_l_p{
    background-color: yellow;
    height: 100%;
  }
  .c10_f_p_l_p{
    background-color: bisque;
    height: 100%;
  }
  
  
  
  
  /*CONTENEDOR PROCUCTOS*/
  .contenedor3_prod1_sesion_productos,
  .contenedor5_prod2_sesion_productos,
  .contenedor7_prod3_sesion_productos {
    float: right;
    display: flex;
    width: 100%;
    margin: 0 auto;
    
  }
  .productos1_sesion_productos,
  .productos2_sesion_productos,
  .productos3_sesion_productos {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  
  .productos1_sesion_productos div,
  .productos2_sesion_productos div,
  .productos3_sesion_productos div {
    width: 100%;
    height: 100%;
    margin: 4px;
  }
  .productos1_sesion_productos div img,
  .productos2_sesion_productos div img,
  .productos3_sesion_productos div img {
    width: 100%;
    height: auto;
  }
  
  
  /*CONTENEDOR PRECIOS PROCUCTOS*/
  .contenedor4_prec1_sesion_productos,
  .contenedor6_prec2_sesion_productos,
  .contenedor8_prec3_sesion_productos {
    display: flex;
    float: right;
    width: 100%;
    margin: 0 auto;
    margin-top: -10px;
  }
  
  .precios1_sesion_productos,
  .precios2_sesion_productos,
  .precios3_sesion_productos {
    width: 100%;
    display: flex;
  }
  .precios1_sesion_productos div,
  .precios2_sesion_productos div,
  .precios3_sesion_productos div {
    width: auto;
    height: auto;
    margin: 4px;
    background-color: rgb(238, 245, 243);/*AQUI*/
    gap: 1px;
  }
  
  
  
  .precios1_sesion_productos div,
  .precios2_sesion_productos div,
  .precios3_sesion_productos div{
    justify-content: space-between;
    width: 100%;
    display: flex;
  }
  
  .precios1_sesion_productos div div h2,
  .precios2_sesion_productos div div h2,
  .precios3_sesion_productos div div h2{
    text-align: left;
  }
  
  .precios1_sesion_productos div div input[type='button'],
  .precios2_sesion_productos div div input[type='button'],
  .precios3_sesion_productos div div input[type='button']{
    width: 100%;
    height: 50%;
    font-size: 12px;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: #bdc3c7;
    position: relative;
    top: 18px;
  }
  
  .precios1_sesion_productos div div input[type='button']:hover,
  .precios2_sesion_productos div div input[type='button']:hover,
  .precios3_sesion_productos div div input[type='button']:hover{
    background-color: #95a5a6;
  }
  
  
  
  .precios1_sesion_productos div div input[type='image'],
  .precios2_sesion_productos div div input[type='image'],
  .precios3_sesion_productos div div input[type='image']{
    width: 30%;
    height: 45%;
    position: relative;
    top: 22px;
    margin-left: 15px;
  }

  .espacio_blanco_hombres{
    text-align: center;
  }
  




  .agrupando_contenedores_parte2_productos{
    display: flex;
    grid-template-columns: 20% 1fr; /* Un 20% para el primer hijo y el resto para el segundo hijo */
  }
  .agrupando_contenedores_sesion1_parte2_productos{
    float: left;
    width: 20%;

  }
  .agrupando_contenedores_sesion2_parte2_productos{
    float: right;
    width: 80%;

  }
  
}