@media only screen and (min-width: 1051px) {
  /* Formulario.css */

.contenedor_formulario{
  background-color: #f5f5f5;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  max-width: 100%;
  width: 400px; /* ancho máximo deseado */
  margin: 0 auto;
}

form {
  max-width: 100%;
}

h2 {
  text-align: center;
}

.input-group {
  margin-bottom: 5px;
}

.n_do, .t_do, .name, .apell, .email, .clave, .sexo {
  display: block;
  margin-bottom: 5px;
  font-weight: 520;
}

.cc, .ti, .ps, .m, .f {
  font-weight: 100;
  font-size: 15px;
}

.input-group input {
  width: 95%;
  height: 10%;
  padding: 6px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
  display: inline-block; /* Añade esta línea */
}

.input-group select {
  width: 99%;
  padding: 6px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.radio_sexo {
  width: 95%;
  padding: 6px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
  display: flex;
  justify-content: center;
  text-align: center;
}

.input-group div label {
  margin-left: 35px;
}

.input-group button {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.input-group button:hover {
  background-color: #0056b3;
}

.secion {
  text-align: right;
  padding: 10px;
}

.ini_sc {
  text-decoration: none;
}

}
