@media only screen and (min-width: 1051px) {

  body {
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
    color: #333;
  }
  .contenedor_nosotros_marca {
    width: 80%;
    margin: auto;
    overflow: hidden;
  }
  
  .contenedor2_titulo_nosotros_marca,
  .contenedores_sobre_nosotros_marca,
  .contenedor_unete_a_nosotros_marca {
    padding: 20px;
    margin: 20px 0;
    background: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .contenedor2_titulo_nosotros_marca h1 {
    text-align: center;
    color: #566573;
  }
  
  .contenedores_sobre_nosotros_marca h2,
  .contenedor_unete_a_nosotros_marca h2 {
    color: #566573;
  }
  
  .contenedores_sobre_nosotros_marca p {
    line-height: 1.6;
  }
  
  .contenedor_unete_a_nosotros_marca {
    text-align: center;
    margin-top: 30px;
  }
  
  .c_u_a_n_m_button {
    display: inline-block;
    padding: 10px 20px;
    background: #3498db;
    color: #fff;
    text-decoration: none;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    font-size: 16px;
    transition: background 0.3s ease;
  }
  
  .c_u_a_n_m_button:hover {
    background: #2980b9;
  }
}