@media only screen and (max-width:1051px) {

  .foto {
    background-size: cover; /* Ajusta la imagen de fondo para cubrir completamente el contenedor */
    background-position: center; /* Centra la imagen de fondo dentro del contenedor */
    background-repeat: no-repeat;
    height: 350px; /* Altura deseada del contenedor */
    width: 100%; /* El contenedor ocupará todo el ancho disponible */
    text-align: center;
    min-height: 200px;
  }

  .foto input[type="submit"] {
    background-color: transparent;
    margin: 50px; /* Añade espacio entre los botones */
    margin-top: 60px;
    color: white;
    border: 2px solid white;
    font-size: 16px;
    cursor: pointer;
    width: 150px; /* Ancho del botón */
    height: 40px; /* Alto del botón */
    transition: background-color 0.3s, color 0.3s, border-color 0.3s;
  }
  .foto input[type="submit"]:hover {
    text-decoration: underline; /* Subraya el texto al pasar el cursor sobre el botón */
    border: transparent;
  }
  .contenedor_inicio {
    width: 100% !important; /* Ancho del contenedor */
    height: 100% !important;
    display: flex !important; /* Utiliza flexbox */
    justify-content: space-between !important; /* Distribuye el espacio entre los elementos */
  }
  
  .contenedor_inicio div {
    width: auto;
    height: 100%; /*PUEDES PONERLO MAS PEQUÑO*/
    overflow: hidden;
    transition: all 500ns ease;
    margin: 1px;
    margin-top: -1px;
  }
  
  .contenedor_inicio div img{
    width: 100%;
    height: 100%;
    transition: all 500ms ease;
  }
  
  .contenedor_inicio div img:hover{
    transform: scale(1.1);
  }
  
}