@media only screen and (min-width: 1051px) {

  .contenedor_menu_header {
    display: flex;
    justify-content: space-between;
    background-color: #333333;
    height: 75px;
  }


  /*SESION 1 DEL MENU*/
  .sesion1_menu_header{
    width: 10%;
    display: flex;
  }

  .logo_pagina_img {
    width: 80px !important; /* Ajusta el tamaño según sea necesario */
    height: auto !important;
    margin-left: 25px !important;
    margin-top: 15px !important;
  
  }


  
  /*SESION 2 DEL MENU*/
  #op {
    position: relative;
    width: 100%; /* Asegura que el contenedor ocupe todo el ancho disponible */
  
  }
  
  #opciones > li {
    display: inline-block;
    margin-right: 40px;
    width: 120px;
    text-align: left;
    position: relative; /* Añade posición relativa para alinear subopciones */
  }
  
  .opcion {
    color: #dddddd;
    display: block;
    text-decoration: none;
    padding: 15px;
    min-width: 110px;
    font-weight: bold;
  }
  
  .opcion:hover {
    color: #bbbbbb;
    font-weight: bolder;
  }
  
  .check-movil{
    display:none;
  }
  
  
  
  .contenedor2 {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
  }
  
  .contenedor2 div {
    width: 25%;
  }
  .contenedor2 > div > h3{
    font-weight: bolder;
    color: #2B2B2B;
    text-align: left;
    margin-left: 25%;
    
  }

  
  
  
  
  /*SESION 3 DEL MENU*/
  .sesion3_menu_header{
    display: flex;
    justify-content: space-between;
    gap: 30px;
  }

  .icono_usu_img{
    width: 35px !important;
    height: 30px !important;
    padding-top: 22px !important;
    margin-right: 25px !important;
    
  }
  
}