@media only screen and (max-width:1051px) {

  .contenedor_menu_header {
    display: flex;
    justify-content: space-between;
    background-color: #333333;
    height: 75px;
  }


  /*SESION 1 DEL MENU*/
  .sesion1_menu_header{
    width: 15%;
    text-align: center;
  }

  .logo_pagina_img {
    width: 50px !important; /* Ajusta el tamaño según sea necesario */
    height: auto !important;
    margin-left: 10px !important;
    margin-top: 20px !important;
  
  }

  .sesion2_menu_header{
    width: 70%;
  }
  
  /*SESION 2 DEL MENU*/
  #op {
    position: relative;
    display: flex;
    width: 100%; /* Asegura que el contenedor ocupe todo el ancho disponible */
  
  }
  
  #opciones > li {
    display: inline-block;
    margin-right: 40px;
    width: 20px;
    font-size: 8pt;
    position: relative; /* Añade posición relativa para alinear subopciones */
  }
  
  .opcion {
    color: #dddddd;
    display: block;
    text-decoration: none;
    padding: 15px;
    min-width: 100px;
    font-weight: bold;
  }
  
  .opcion:hover {
    color: #bbbbbb;
    font-weight: bolder;
  }
  
  .check-movil{
    display:none;
  }
  .sesion3_menu_header{
    width: 12%;
    position: relative;
    text-align: center;
  }
  
  /*SESION 3 DEL MENU*/
  .icono_usu_img{
    width: 30px !important;
    height: 25px !important;
    padding-top: 22px !important;
    margin-left: 5px;
    
  }
  
}

